.contact {
   max-width: 1224px;
   min-height: 100vh;
   padding: 112px 24px 0px 24px;
   margin: 0 auto;

   @media screen and (max-width: 600px) {
      padding: 112px 16px 50px 16px;
   }

   &__title {
      text-align: center;

      span {
         color: #e1094a;
         text-transform: uppercase;
         font-size: 12px;
         letter-spacing: 5px;

         @media screen and (max-width: 750px) {
            font-size: 10px;
         }
      }

      h1 {
         margin-bottom: 48px;
         color: #fff;
         font-size: 50px;

         @media screen and (max-width: 750px) {
            font-size: 30px;
         }
      }
   }

   &__form {
      max-width: 1100px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      background: #fff;
      border-radius: 4px;
      padding: 32px;
      margin-left: auto;
      margin-right: auto;
      gap: 32px;
      overflow: hidden;

      @media screen and (max-width: 600px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }

   &__left_container {

      h3 {
         font-size: 24px;
         margin-bottom: 16px;
      }

      p {
         font-size: 14px;
         margin-bottom: 16px;
         margin: 0;
      }
   }

   &__left {
      display: flex;
      align-items: center;
      margin-top: 16px;

      div {
         width: 50px;
         height: 50px;
         display: flex;
         justify-content: center;
         align-items: center;
         background: #e1094a;
         border-radius: 50%;
         color: #fff;
         margin-right: 16px;
      }

      p {
         font-size: 14px;
         margin: 0;
      }
   }

   &__icons {
      display: flex;
      margin-top: 16px;

      svg {
         width: 20px;
         height: 20px;
      }

      a {
         color: #17171C;
         margin: 7px;
         transition: .4s;

         :hover {
            transform: translateY(3px);
         }
      }
   }

   &__right {
      h3 {
         font-size: 24px;
         margin-bottom: 16px;
      }

      div {
         width: 100%;
         display: grid;
         grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
         gap: 8px;
      }

      input {
         margin-bottom: 8px;
         border: none;
         background: #f3f3f3;
         padding: 16px 24px;
         border-radius: 4px;
         font-size: 14px;
         outline: none;
      }

      textarea {
         border: none;
         background: #f3f3f3;
         padding: 16px 24px;
         margin-bottom: 8px;
         border-radius: 4px;
         font-size: 14px;
         min-height: 150px;
         outline: none;
         resize: none;
         overflow: hidden;
      }
   }

   &__btn {
      margin-top: 16px;
      background: #e1094a;
      border-radius: 4px;
      padding: 10px 16px;
      font-size: 14px;
      max-width: 100px;

      a {
         text-decoration: none;
         color: #fff;
      }
   }


}